:root {
  --color-contrast-light: #fff;
  --color-contrast-dark: #261f1f;
  --color-background:var(--color-contrast-light, #fff);
  --color-text:var(--color-contrast-dark, #261f1f);
  --color-text-light: #735e5e;
  --color-border: #958585;
  --color-border-light: #d8d2d2;
  --color-action: #d10300;
  --color-action-hover: #5f1615;
  --color-accent: #d6b361;
  --color-accent-light: #f8f2e3;
  --color-quote: #e4ce99;
  --color-verse: #efd3c2;
  --color-active: #f9e1e0;
  font-family: open, system, system-ui, sans-serif;
  background-color: var(--color-background, #fff);
  color: var(--color-contrast-dark, #261f1f);
  font-size: calc(16px + 0.25vw);
  line-height: 1.6;
}

